/* @font-face rule to import Montserrat font */
@font-face {
    font-family: 'Montserrat';
    src: url('../../fonts/Montserrat-Regular.ttf') format('truetype');
    /* Add additional font weights and styles if needed */
    font-weight: normal;
    font-style: normal;
  }
  @font-face {
    font-family: 'Montserrat-Bold';
    src: url('../../fonts/Montserrat-SemiBold.ttf') format('truetype');
    /* Add additional font weights and styles if needed */
    font-weight: normal;
    font-style: normal;
  }
  
  /* Ensure html and body take up full height */
  html, body {
    height: 100%;
    margin: 0;
    font-family: 'Montserrat', sans-serif; /* Specify Montserrat as the default font */
  }
  
  
  
  /* Set desktop container to full height and use flexbox to manage layout */
  .desktop {
    background-color: #003851;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; /* Make sure desktop takes up the full viewport height */
    box-sizing: border-box;
    
  }
  
  /* Ensure overlap-group fills the desktop */
  .desktop .overlap-group {
    background-image: url(../../BG-Effect.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
  }
  
  .desktop .cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    justify-content: center;
    align-items: center;
    height: 298px;
    width: 100%;
    max-width: 1200px; /* Adjust max-width as needed */
    padding: 20px; /* Adjust padding as needed */
    justify-items: center;
  }
  
  .desktop .element-card,
  .desktop .element-card-2,
  .desktop .element-card-3 {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 12px;
    width: 232px;
  }
  
  .desktop .graphic {
    height: 232px;
    width: 100%;
    object-fit: cover; /* Ensure images cover their container */
  }
  
  .desktop .copy {
    text-align: center;
  }
  
  .desktop .nav-bar {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 54px;
    width: 100%;
    max-width: 1200px; /* Adjust max-width as needed */
    padding: 10px; /* Adjust padding as needed */
  }
  
  .desktop .element-secondary-button {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ddf482;
    border-radius: 8px;
    height: 40px;
    width: 116px;
    margin-right: 10px; /* Adjust margin as needed */
  }
  
  .desktop .text-wrapper-2 {
    color: #003851;
    font-family: "Montserrat-Bold", Helvetica;
    font-size: 20.5px;
    font-weight: 600;
  }
  
  .desktop .text-wrapper-3 {
    color: #eae4d7;
    font-family: "Montserrat", Helvetica;
    font-size: 20.5px;
    font-weight: 600;
  }
  
  .desktop .logo-and-font {
    display: flex;
    align-items: center;
  }
  
  .desktop .text-wrapper-4,
  .desktop .text-wrapper-5 {
    color: #eae4d7;
    font-family: "Montserrat", Helvetica;
    font-size: 20.5px;
    font-weight: 700;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .desktop .logo-and-font-for {
    height: 54px;
    width: 54px;
    object-fit: cover; /* Ensure logo fits correctly */
  }

  /* Prevent purple underline on visited links */
a {
  text-decoration: none;
  color: inherit; /* Use the same color as the text */
}

a:visited {
  text-decoration: none;
   /* Ensure visited links have the same color as the text */
}